.signIn {
  display: flex;
  flex-direction: column;

  @media (min-width: 768px) {
    flex-direction: row;
  }
}

.animation_signIn {
  width: 100%;

  @media (min-width: 768px) {
    width: 50%;
  }
}

.form_signIn {
  width: 100%;
  margin: auto 0;
  @media (min-width: 768px) {
    width: 50%;
  }
}
.animation_signIn {
  display: none;

  @media (min-width: 768px) {
    display: flex;
    width: 50%;
  }
}


